import { createElement, useState } from 'rax';
import { renderToRoot } from '../utils/render';
import Dialog from './dialog';
const DialogWrapper = (props) => {
    const { onClose } = props;
    const [visible, setVisible] = useState(true);
    const handleClose = (reason, e) => {
        setVisible(false);
        setTimeout(() => {
            if (onClose) {
                onClose(reason, e);
            }
        }, 300);
    };
    return <Dialog {...props} onClose={handleClose} visible={visible} disablePortal/>;
};
const hideStack = [];
export const show = (props) => {
    const emptyFn = () => { };
    let { onClose = emptyFn } = props;
    const hideCurrent = renderToRoot(<DialogWrapper {...props} onClose={(reason, e) => {
        onClose(reason, e);
        onClose = emptyFn;
        hideCurrent();
    }}/>, () => onClose('api', null));
    hideStack.push(hideCurrent);
    return {
        hide: hideCurrent,
    };
};
export const hide = () => {
    while (hideStack.length) {
        const h = hideStack.pop();
        if (h) {
            h.call(null);
        }
    }
};
export const alert = (props) => {
    return show(Object.assign(Object.assign({}, props), { type: 'alert' }));
};
export const confirm = (props) => {
    return show(Object.assign(Object.assign({}, props), { type: 'confirm' }));
};
