import { createElement } from 'rax';
import BasePicker from './base-picker';
import EmbedMinutePicker from '../embed-picker/minute-picker';
export default class MinutePicker extends BasePicker {
    renderPicker() {
        const { validRange } = this.props;
        return (<EmbedMinutePicker value={this.state.pickerValue} disabledDate={this.props.disabledDate} validRange={validRange} onChange={this.handleChange}/>);
    }
    get format() {
        return this.props.format;
    }
    getDefaultFormat() {
        return '';
    }
}
MinutePicker.defaultProps = {
    format: 'YYYY-MM-DD HH:mm',
};
