import classNames from 'classnames';
import dayjs from 'dayjs';
import { createElement, Fragment } from 'rax';
import Text from 'rax-text';
import View from '../view';
import { CellStatusType } from './types';
import { getCellStatus, getCurrentMonthList, getDayList, getMonthList, isTheSameMonth, isHaveDate } from './utils';
const YEAR_ROW_CELLS = 3;
const MONTH_ROW_CELLS = 7;
const CalendarContent = (props) => {
    const { selectedDate, visibleDate, mode, type, maxDate, minDate, onDateChange = () => { }, disabledDate, enableTimePicker, locale, prefix, renderCellBottom, renderCellRight, dateCellRender, monthCellRender, firstDay = 0, } = props;
    const onItemClick = (newDate) => {
        if (!newDate) {
            return;
        }
        if (type === 'multi') {
            if (!Array.isArray(selectedDate)) {
                if (onDateChange) {
                    onDateChange([newDate], newDate);
                }
                return;
            }
            const vIndex = isHaveDate(newDate, selectedDate);
            if (vIndex === -1) {
                selectedDate.push(newDate);
                if (onDateChange) {
                    onDateChange(selectedDate, newDate);
                }
                return;
            }
            selectedDate.splice(vIndex, 1);
            if (onDateChange) {
                onDateChange(selectedDate, newDate);
            }
            return;
        }
        if (type === 'range') {
            if (!Array.isArray(selectedDate)) {
                return;
            }
            if (selectedDate.length === 0) {
                if (onDateChange) {
                    onDateChange([newDate], newDate);
                }
                return;
            }
            if (selectedDate.length === 2) {
                if (onDateChange) {
                    onDateChange([newDate], newDate);
                }
                return;
            }
            if (newDate.getTime() < selectedDate[0].getTime()) {
                if (onDateChange) {
                    onDateChange([newDate, selectedDate[0]], newDate);
                }
                return;
            }
            if (onDateChange) {
                onDateChange([selectedDate[0], newDate], newDate);
            }
            return;
        }
        if (onDateChange) {
            onDateChange(newDate, newDate);
        }
        return;
    };
    const renderTopArea = () => {
        if (mode === 'year') {
            return null;
        }
        const headPrefix = `${prefix}calendar-content-top`;
        const weeks = getDayList(locale);
        const tops = [];
        for (let i = 0; i < weeks.length; i++) {
            const index = firstDay + i > 6 ? firstDay + i - 7 : firstDay + i;
            if (!weeks[index]) {
                continue;
            }
            tops.push(<Text className={`${headPrefix}-item`} key={`${headPrefix}-item-${weeks[index].value}`}>
          {weeks[index].label}
        </Text>);
        }
        return <View className={`${headPrefix}`}>{tops}</View>;
    };
    const renderYearContent = () => {
        const totalMonths = getMonthList(visibleDate, locale);
        const rowCount = totalMonths.length / YEAR_ROW_CELLS;
        const rows = [];
        for (let i = 0; i < rowCount; i++) {
            const datas = totalMonths.slice(i * YEAR_ROW_CELLS, (i + 1) * YEAR_ROW_CELLS);
            rows.push(renderRow(datas, i === 0 ? true : false, i));
        }
        const headPrefix = `${prefix}calendar-content-real`;
        return <View className={classNames(`${headPrefix}`, `${headPrefix}-year`)}>{rows}</View>;
    };
    const renderYearCell = (cellDate, isFirstLine) => {
        let cellType = getCellStatus(cellDate && cellDate.value, selectedDate, mode, type, disabledDate, maxDate, minDate, 'year');
        const cellPrefix = `${prefix}calendar-cell`;
        if (cellType === CellStatusType.begin) {
            if (Object.prototype.toString.call(selectedDate) === '[object Array]') {
                if (selectedDate.length === 1) {
                    cellType = CellStatusType.selected;
                }
            }
        }
        return (<View key={cellDate && cellDate.label} className={classNames(cellPrefix, `${cellPrefix}-${mode}`, {
            [`${cellPrefix}-${mode}-${cellType}`]: isFirstLine === false,
            [`${cellPrefix}-${mode}-firstline-${cellType}`]: isFirstLine === true,
        })} onClick={() => {
            if (cellType === CellStatusType.disabled) {
                return;
            }
            if (onItemClick) {
                onItemClick(cellDate && cellDate.value);
            }
        }}>
        <Text className={classNames(`${cellPrefix}-${mode}-text-${cellType}`)}>{cellDate && cellDate.label}</Text>
      </View>);
    };
    const renderDateCell = (cellDate, rowBegin, rowEnd, rowCount) => {
        let cellType = getCellStatus(cellDate && cellDate.value, selectedDate, mode, type, disabledDate, maxDate, minDate, 'date');
        const realDisable = cellType === CellStatusType.disabled;
        const cellPrefix = `${prefix}calendar-cell`;
        if (cellType === CellStatusType.begin) {
            if (Object.prototype.toString.call(selectedDate) === '[object Array]') {
                if (selectedDate.length === 1) {
                    cellType = CellStatusType.selected;
                }
            }
        }
        if (!isTheSameMonth(cellDate && cellDate.value, visibleDate) &&
            cellType !== CellStatusType.selected &&
            cellType !== CellStatusType.begin &&
            cellType !== CellStatusType.end &&
            cellType !== CellStatusType.range) {
            cellType = CellStatusType.disabled;
        }
        let rowString = '';
        if (rowBegin) {
            rowString = '-row-begin';
        }
        if (rowEnd) {
            rowString = '-row-end';
        }
        return (<View key={`date-cell-${cellDate.label}`} className={classNames(`${cellPrefix}${rowString}`, `${cellPrefix}-${mode}`, {
            [`${cellPrefix}-${mode}--short`]: rowCount === 6,
        })} onClick={() => {
            if (realDisable) {
                return;
            }
            if (onItemClick) {
                onItemClick(cellDate && cellDate.value);
            }
        }}>
        <View className={classNames(`${cellPrefix}-left-${cellType}`, `${cellPrefix}${rowString}-left-${cellType}`)}/>
        <View className={classNames(`${cellPrefix}-right-${cellType}`, `${cellPrefix}${rowString}-right-${cellType}`)}/>
        <Text className={classNames(`${cellPrefix}-text`, `${cellPrefix}-text-${cellType}`, `${cellPrefix}${rowString}-text-${cellType}`, {
            [`${cellPrefix}-text--short`]: rowCount === 6,
        })}>
          {cellDate && cellDate.label}
        </Text>
        {renderCellBottom ? (<View className={classNames(`${cellPrefix}-contain-bottom-${cellType}`, `${cellPrefix}${rowString}-contain-bottom-${cellType}`)}>
            {renderCellBottom(cellDate && cellDate.value)}
          </View>) : null}
        {renderCellRight ? (<View className={classNames(`${cellPrefix}-contain-right-${cellType}`, `${cellPrefix}${rowString}-contain-right-${cellType}`)}>
            {renderCellRight(cellDate && cellDate.value)}
          </View>) : null}
      </View>);
    };
    const renderRow = (datas = [], firstLine, key, rowCount = 5) => {
        const cells = [];
        for (let i = 0; i < datas.length; i++) {
            if (!datas[i]) {
                continue;
            }
            if (mode === 'year') {
                if (monthCellRender) {
                    cells.push(<Fragment key={i}>{monthCellRender(datas[i] && datas[i].value)}</Fragment>);
                }
                else {
                    cells.push(<Fragment key={i}>{renderYearCell(datas[i], firstLine)}</Fragment>);
                }
            }
            else {
                if (dateCellRender) {
                    cells.push(<Fragment key={i}>{dateCellRender(datas[i] && datas[i].value)}</Fragment>);
                }
                else {
                    cells.push(renderDateCell(datas[i], i === 0 ? true : false, i === datas.length - 1 ? true : false, rowCount));
                }
            }
        }
        const headPrefix = `${prefix}calendar-content-row`;
        return (<View key={`${headPrefix}-${mode}-${key}`} className={classNames(`${headPrefix}`, `${headPrefix}-${mode}`, {
            [`${headPrefix}--short`]: rowCount === 6,
        })}>
        {cells}
      </View>);
    };
    const renderMonthContent = () => {
        const totalDays = getCurrentMonthList(visibleDate, locale);
        const rowCount = totalDays.length / MONTH_ROW_CELLS;
        const rows = [];
        for (let i = 0; i < rowCount; i++) {
            const datas = totalDays.slice(i * MONTH_ROW_CELLS, (i + 1) * MONTH_ROW_CELLS);
            rows.push(renderRow(datas, i === 0 ? true : false, i, rowCount));
        }
        const headPrefix = `${prefix}calendar-content-real`;
        return <View className={classNames(`${headPrefix}`, `${headPrefix}-month`)}>{rows}</View>;
    };
    const renderWeekContent = () => {
        const rows = [];
        const fD = dayjs(visibleDate).day(firstDay);
        const datas = new Array(7).fill(0).map((key, index) => {
            const day = fD.add(index, 'day');
            return {
                label: day.date().toString(),
                value: new Date(day.valueOf()),
            };
        });
        rows.push(renderRow(datas, true, 0));
        const headPrefix = `${prefix}calendar-content-real`;
        return <View className={classNames(`${headPrefix}`, `${headPrefix}-week`)}>{rows}</View>;
    };
    const renRealContent = () => {
        let rContent = null;
        switch (mode) {
            case 'year':
                rContent = renderYearContent();
                break;
            case 'week':
                rContent = renderWeekContent();
                break;
            case 'month':
            default:
                rContent = renderMonthContent();
                break;
        }
        return rContent;
    };
    const top = renderTopArea();
    const content = renRealContent();
    return (<View className={classNames(`${prefix}calendar-content`, `${prefix}calendar-content-${mode}`, {
        [`${prefix}calendar-content--no-bottom`]: enableTimePicker,
    })}>
      {top}
      {content}
    </View>);
};
export default CalendarContent;
