import { createElement, useContext } from 'rax';
import List from '../../list';
import Menu from '../../menu';
import TagSelect from '../../tag-select';
import { findInArray, isNil } from '../../utils';
import View from '../../view';
import SelectContext from '../context';
import Option from '../option';
import { defaultLocalFilter, isSelected, removeItemFromArray } from '../util';
const SelectBody = (props) => {
    const { prefix, size, displayType, disabled, mode, dataSource, filterLocal = true, filter = defaultLocalFilter, searchValue, innerValue, onChange, } = useContext(SelectContext);
    const renderWithTag = displayType === 'tag';
    const renderWithMenu = !renderWithTag && mode === 'single';
    const filteredDataSource = dataSource.filter((item) => {
        if (filterLocal) {
            return filter(searchValue, item);
        }
        return true;
    });
    if (renderWithMenu) {
        return (<Menu prefix={prefix} size={size} selectMode="single" dataSource={filteredDataSource} selectedKeys={innerValue.map((it) => it.value)} onItemClick={(_, item) => onChange([{ label: item.label, value: item.value }])}/>);
    }
    if (renderWithTag) {
        return (<View className={`${prefix}select--tag-${size}`}>
        <TagSelect prefix={prefix} mode={mode} size={size} value={innerValue.map((it) => it.value)} dataSource={filteredDataSource} onChange={(v) => {
            if (isNil(v)) {
                onChange([]);
                return;
            }
            const newValue = Array.isArray(v) ? v : [v];
            const newItems = newValue.map((x) => {
                return findInArray(filteredDataSource, (item) => {
                    return item.value === x;
                });
            });
            onChange(newItems);
        }}/>
      </View>);
    }
    return (<List>
      {filteredDataSource.map((it) => {
        return (<Option key={it.value} selected={isSelected(innerValue, it.value)} disabled={disabled || it.disabled} onClick={() => {
            const selectedItem = findInArray(innerValue, (item) => {
                return item.value === it.value;
            });
            if (selectedItem) {
                onChange(removeItemFromArray(innerValue, selectedItem.value));
            }
            else {
                onChange([
                    ...innerValue,
                    {
                        label: it.label,
                        value: it.value,
                    },
                ]);
            }
        }} {...it}>
            {it.label}
          </Option>);
    })}
    </List>);
};
export default SelectBody;
